/* Blockquote SASS
----------------------------------------------*/
blockquote {
  border-left: none;
  font-weight: bold;
  padding-left: 4rem;
  max-width: 75ch;
  /* Signatures (beneath content) */ }

blockquote.ws2-element-alt {
  padding: 24px 24px 24px 4rem; }

blockquote:before {
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  content: '';
  min-height: 3rem;
  min-width: 2.375rem;
  float: left;
  margin-left: -4rem;
  color: #d0d0d0; }

blockquote > p:not([class*=sig-]):after {
  content: '"'; }

blockquote.ws2-element-maroon:before {
  background-image: url(data:image/svg+xml;base64,); }

blockquote.ws2-element-gold:before {
  background-image: url(data:image/svg+xml;base64,); }

blockquote.ws2-element-white:before {
  background-image: url(data:image/svg+xml;base64,); }

blockquote.ws2-bq-image {
  overflow: auto;
  padding-left: 14.3625rem; }

blockquote.ws2-bq-image img:first-of-type {
  -webkit-clip-path: circle(90px);
  clip-path: circle(90px);
  float: left;
  position: absolute;
  margin-left: -14.375rem; }

blockquote.ws2-bq-image:before {
  font-family: Arial, Helvetica, "Nimbus Sans L", "Liberation Sans", FreeSans, sans-serif;
  font-size: initial;
  content: initial;
  margin-left: -14.3625rem;
  color: #d0d0d0; }

blockquote .ws2-sig-title, blockquote .ws2-sig-byline {
  font-size: 16px;
  font-style: inherit; }

blockquote .ws2-sig-title:before {
  padding-right: 0.5em;
  content: "\2014"; }

blockquote .ws2-sig-byline {
  clear: left;
  display: block;
  font-weight: normal;
  padding-left: 1.5em; }

